import React, { Component } from 'react'
import PropTypes from 'prop-types'
class Search extends Component {
    state = {
        result: this.props.list
    }

    handleChane = (event) => {
        const {value} = event.target;
        const arr = this.props.list;
        let temparr = [];
        arr.forEach(element => {
            let tempStr =  element.toLowerCase();
            if(tempStr.includes(value.trim())){
                temparr.push(element);
            }
        });
        if(temparr.length === 0 ){
            temparr.push("No location")
        }
        this.setState({
            result: temparr
        });
    }

    render() {
        const resultArray = this.state.result.map((ele) => (<h1>{ele}</h1>) );
        return (
            <div>
                <form className="myform" autoComplete="off">
                    <input className="inputSearch" 
                        type="text" 
                        name="result" 
                        onChange={this.handleChane}/>
                    
                </form>
                <div className="list-elements">{resultArray}</div>
            </div>
        )
    }
}

Search.propTypes = {
    list: PropTypes.array
}

export default Search;
